<template>
  <el-date-picker
    v-model="showRange"
    type="daterange"
    range-separator="至"
    start-placeholder="开始日期"
    end-placeholder="结束日期"
    :picker-options="dataRangePickerOptions"
    @change="onChanged"
    >
  </el-date-picker>
</template>

<script>
import Moment from 'moment'

export default {
  name: 'DateRangeGeLt',
  props: {
    'valueFormat': {
      default: 'YYYY-MM-DD HH:mm:ss'
    },
    'rangeGeLt': {
      type: Array,
      twoWay: true
    },
    'shortCutLastDay': Boolean,
    'shortCutLastWeek': Boolean,
    'shortCutLastMonth': Boolean,
    'shortCutLastYear': Boolean,
    'noShortcuts': Boolean
  },
  data () {
    return {
      dataRangePickerOptions: {
        shortcuts: [
          {
            text: '昨天',
            onClick (picker) {
              const end = new Date()
              const start = Moment().subtract(1, 'd')
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近一周',
            onClick (picker) {
              const end = new Date()
              const start = Moment().subtract(7, 'd')
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date()
              const start = Moment().subtract(1, 'm')
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      showRange: null
    }
  },
  created () {
  },
  mounted () {
    if (this.rangeGeLt && this.rangeGeLt.length === 2) {
      this.showRange = [Moment(this.rangeGeLt[0]), Moment(this.rangeGeLt[1]).subtract(1, 'd')]
    } else {
      this.showRange = null
    }
  },
  methods: {
    onChanged: function (dateRangeGeLe) {
      let newRangeGeLt
      if (dateRangeGeLe && dateRangeGeLe.length === 2) {
        newRangeGeLt = [
          Moment(dateRangeGeLe[0]).format(this.valueFormat),
          Moment(dateRangeGeLe[1]).add(1, 'd').format(this.valueFormat)
        ]
      } else {
        newRangeGeLt = null
      }
      this.$emit('input', newRangeGeLt)
    }
  }
}
</script>

<style scoped>
</style>



