<template>
  <div>
    <!-- .el-table的左右padding如果不为0，会影响内部宽度计算 -->
    <el-table class="section" ref="mainTable" :data="tableData" :stripe=true :border=true :size="size"
        highlight-current-row
        @current-change="tableCurrentChanged"
        @selection-change="tableSelectionChanged"
        v-loading="loading"
        >
      <el-table-column type="selection" width="40" />
      <slot></slot>
    </el-table>
    <el-pagination
      class="section"
      @size-change="pagingSizeChanged"
      @current-change="pagingCurrentChanged"
      :current-page="paging.currentPage"
      :page-sizes="[10, 20, 50, 100, 200, 500, 1000]"
      :page-size="paging.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="dataTotal">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'PagingTable',
  props: {
    multiMode: {
      type: Boolean,
      default: false
    },
    initPageSize: {
      type: Number,
      default: 20
    },
    cacheDataTotal: {
      type: Boolean,
      default: false
    },
    size: {
      default: 'small'
    }
  },
  data () {
    return {
      tableData: [],
      tableSelected: null,
      dataTotal: 0,
      paging: {
        pageSize: this.initPageSize ? this.initPageSize : 50,
        currentPage: 1,
        dataTotal: 0 // this dataTotal is only for request param
      },
      loading: true
    }
  },
  created () {
  },
  mounted () {
    if (this.initPageSize) {
      this.paging.pageSize = this.initPageSize
    }
  },
  methods: {
    load: function () {
      this.$emit('input', false)
      this.$emit('input', null)
      this.loading = true
      this.$emit('loadFunc', this.paging, this.setPagingList)
    },
    list: function () {
      return this.tableData
    },
    setPagingList: function (pagingList) {
      this.dataTotal = pagingList.paging.dataTotal
      if (this.cacheDataTotal) {
        this.paging.dataTotal = pagingList.paging.dataTotal
      }
      this.tableData = pagingList.list
      this.loading = false
      this.tableSelected = null
      this.$emit('input', false)
      this.$emit('input', null)
    },
    pagingSizeChanged: function (newPageSize) {
      this.paging.pageSize = newPageSize
      this.load()
    },
    pagingCurrentChanged: function (newCurrentPage) {
      this.paging.currentPage = newCurrentPage
      this.load()
    },
    tableSelectionChanged: function (rows) {
      if (this.multiMode) {
        this.tableSelected = rows
      } else {
        this.tableSelected = rows.length === 1 ? rows[0] : null
      }
      this.$emit('input', this.tableSelected)
    },
    tableCurrentChanged: function (row) {
      if (row) {
        if (this.multiMode) {
          this.$refs.mainTable.toggleRowSelection(row)
        } else {
          if (this.tableSelected === row) {
            this.$refs.mainTable.clearSelection()
          } else {
            this.$refs.mainTable.clearSelection()
            this.$refs.mainTable.toggleRowSelection(row)
          }
        }
        this.$refs.mainTable.setCurrentRow()
        this.$emit('input', this.tableSelected)
      }
    },
    clearSelection: function () {
      this.$refs.mainTable.clearSelection()
      this.$refs.mainTable.setCurrentRow()
    },
    loadFirstPage () {
      this.paging.currentPage = 1
      this.load()
    }
  }
}
</script>

<style scoped>
</style>



